import { AbstractLink, HyperLinkProps } from './abstract.link';

export class JSLink extends AbstractLink {
    protected jsContent: string;
    public constructor(props: JSLinkProps) {
        super(props);
        const { jsContent } = props;
        this.jsContent = jsContent;
    }

    protected _excute() {
        new Function(this.jsContent)();
    }
}

export interface JSLinkProps extends HyperLinkProps {
    jsContent: string;
}
